:root {
  --color-bg: #ffffff;
  --color-text: #212121;
  --color-muted: #515151;
  --color-accent: #425154;
  --color-surface: #d9d9d9;
  --radius: 10px;
  --gap: 16px;
  --maxw: 1080px;
}

[data-theme="dark"] {
  --color-bg: #121212;
  --color-text: #ffffff;
  --color-muted: #d9d9d9;
  --color-accent: #425154;
  --color-surface: #1e1e1e;
}

html, body, #root { height: 100%; }
body {
  margin: 0;
  background: var(--color-bg);
  color: var(--color-text);
  font-family: 'Montserrat', system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, 'Helvetica Neue', Arial, 'Apple Color Emoji', 'Segoe UI Emoji';
  line-height: 1.5;
}

.container {
  /* Always keep breathing room (~90%) on all viewports */
  width: min(90%, var(--maxw));
  margin: 0 auto;
  padding: 0 16px;
}

.app-shell {
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

a { color: var(--color-accent); text-decoration: none; }
a:hover { text-decoration: underline; }

.card {
  background: var(--color-surface);
  border-radius: var(--radius);
  padding: 16px;
}

.sr-only {
  position: absolute; width: 1px; height: 1px; padding: 0; margin: -1px; overflow: hidden; clip: rect(0,0,0,0); white-space: nowrap; border: 0;
}
